import React, { Component } from 'react'
import Progress from '../Progress/Loadable';
import Profile from '../Profile/Loadable';
import Terms from '../TermsAndConditions/Loadable';

import Header from '../../components/Header';


export default class index extends Component {
    state = {
        step: 1,
        headerTexts: ['Business Website', 'Business Details', 'Terms and Conditions']
    }

    next = () => {
        this.setState({step: this.state.step + 1})
    }
    
    back = () => {
        if(this.state.step === 1) return
        this.setState({step: this.state.step - 1})
    }

    render() {
        const {step} = this.state;
        return (
            <>
                <Header next={this.next} back={this.back} step={this.state.step} headerText={this.state.headerTexts[step - 1]} />
                {step === 1 && <Progress next={this.next} back={this.back}/>}
                {step === 2 && <Profile next={this.next} back={this.back}/>}
                {step === 3 && <Terms next={this.next} back={this.back}/>}
            </>
        )
    }
}
