import React, { Component } from 'react'

export default class ProgressIndicator extends Component {
  render() {
    return (
      <div>
        {[0,1,2,3].map((_, index) => (
          <span key={index} className={`dot ${this.props.step - 1 === index ? 'active': ''}`}></span>
        ))}
      </div>
    )
  }
}
