import React, { Component } from 'react'
import BackIcon from './assets/ic-backBlack.png';
import './style.css';
import ProgressIndicator from './ProgressIndicator';


export default class Header extends Component {
  render() {
    return (
     <div className="container">
        <div className="row">
            <div className="col-2 align-self-center">
            <img onClick={this.props.back} className="header-icon responsive" src={BackIcon} alt="back"/>
            </div>
            <div className="col-7 align-self-center">
            <h2 className="header-title">{this.props.headerText}</h2>
            </div>
            <div className="col-3 align-self-center">
            <ProgressIndicator step={this.props.step} />
            </div>
        </div>
      </div>
    )
  }
}
